"use client"

import { useState, useEffect } from "react"
import { ArrowLeft, CheckCircle2, Eye, EyeOff, RotateCcw, HelpCircle } from "lucide-react"

interface PuzzleGameProps {
  onComplete: () => void
  onBack: () => void
}

interface Tile {
  id: number
  correct: number
  pos: number
}

export function PuzzleGame({ onComplete, onBack }: PuzzleGameProps) {
  const [tiles, setTiles] = useState<Tile[]>([])
  const [selected, setSelected] = useState<number | null>(null)
  const [moves, setMoves] = useState(0)
  const [complete, setComplete] = useState(false)
  const [showNumbers, setShowNumbers] = useState(true)
  const [showHint, setShowHint] = useState(false)

  // Initialize puzzle
  const init = () => {
    const base: Tile[] = Array.from({ length: 9 }, (_, i) => ({
      id: i,
      pos: i,
      correct: i,
    }))

    // Shuffle positions
    const shuffled = [...base.map(t => t.pos)]
    for (let i = shuffled.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1))
      ;[shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]]
    }

    const final = base.map((tile, i) => ({ ...tile, pos: shuffled[i] }))

    setTiles(final)
    setMoves(0)
    setComplete(false)
    setSelected(null)
  }

  useEffect(() => {
    init()
  }, [])

  const sortedTiles = [...tiles].sort((a, b) => a.pos - b.pos)

  const onTileClick = (tileId: number) => {
    if (complete) return

    if (selected === null) {
      setSelected(tileId)
      return
    }

    if (selected === tileId) {
      setSelected(null)
      return
    }

    // Swap logic FIXED
    setTiles(prev => {
      const t1 = prev.find(t => t.id === selected)!
      const t2 = prev.find(t => t.id === tileId)!
      const newTiles = [...prev]

      const temp = t1.pos
      t1.pos = t2.pos
      t2.pos = temp

      return newTiles
    })

    setSelected(null)
    setMoves(m => m + 1)
  }

  // Completion check
  useEffect(() => {
    if (tiles.length && tiles.every(t => t.pos === t.correct)) {
      setComplete(true)
    }
  }, [tiles])

  return (
    <div className="text-center relative pb-12">

      {/* Back button */}
      <button
        onClick={onBack}
        className="absolute top-4 left-4 flex items-center gap-2 text-muted-foreground hover:text-foreground"
      >
        <ArrowLeft className="w-5 h-5" /> Back
      </button>

      <h2 className="font-serif text-3xl mt-10 mb-4">Puzzle Reveal</h2>

      {/* Controls */}
      <div className="flex justify-center gap-3 mb-4">
        <button
          onClick={() => setShowNumbers(s => !s)}
          className="px-4 py-2 bg-secondary/50 rounded-full text-sm flex gap-2"
        >
          {showNumbers ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
          {showNumbers ? "Hide Numbers" : "Show Numbers"}
        </button>

        <button
          onMouseDown={() => setShowHint(true)}
          onMouseUp={() => setShowHint(false)}
          onTouchStart={() => setShowHint(true)}
          onTouchEnd={() => setShowHint(false)}
          className="px-4 py-2 bg-primary/20 rounded-full text-sm flex gap-2"
        >
          <Eye className="w-4 h-4" />
          Hold to View
        </button>

        <button
          onClick={init}
          className="px-4 py-2 bg-secondary/50 rounded-full text-sm flex gap-2"
        >
          <RotateCcw className="w-4 h-4" /> Restart
        </button>
      </div>

      {/* Hint overlay */}
      {showHint && (
        <div className="absolute inset-0 bg-black/40 flex items-center justify-center z-20 backdrop-blur-sm">
          <img src="/1.jpg" className="w-72 h-72 rounded-xl object-cover border-4 border-primary shadow-xl" />
        </div>
      )}

      {/* Puzzle Grid */}
      <div className="glass-card rounded-3xl p-6 inline-block">
        <div className="grid grid-cols-3 gap-2 w-72 h-72">
          {sortedTiles.map(tile => {
            const row = Math.floor(tile.correct / 3)
            const col = tile.correct % 3

            return (
              <button
                key={tile.id}
                onClick={() => onTileClick(tile.id)}
                className={`relative rounded-lg overflow-hidden transition-all ${
                  selected === tile.id
                    ? "ring-4 ring-primary scale-95"
                    : "hover:scale-[1.04]"
                }`}
              >
                <div
                  className="w-full h-full bg-cover bg-no-repeat"
                  style={{
                    backgroundImage: "url(/1.jpg)",
                    backgroundSize: "300px 300px",
                    backgroundPosition: `-${col * 100}px -${row * 100}px`,
                  }}
                />

                {showNumbers && !complete && (
                  <div className="absolute top-1 left-1 bg-black/60 text-white text-xs w-6 h-6 flex items-center justify-center rounded-full">
                    {tile.correct + 1}
                  </div>
                )}

                {tile.pos === tile.correct && !complete && (
                  <CheckCircle2 className="absolute bottom-1 right-1 text-green-500 w-5 h-5" />
                )}
              </button>
            )
          })}
        </div>

        <div className="mt-3 text-sm text-muted-foreground">
          {tiles.filter(t => t.pos === t.correct).length} / 9 correct
        </div>
      </div>

      {complete && (
        <div className="mt-8">
          <p className="text-primary text-lg mb-3">Perfect! Puzzle Completed ❤️</p>
          <button
            onClick={onComplete}
            className="px-8 py-4 bg-primary text-white rounded-full shadow-lg hover:scale-105"
          >
            Continue
          </button>
        </div>
      )}
    </div>
  )
}
