"use client"

import type React from "react"
import { useState, useEffect, useCallback } from "react"
import { SparkleEffect } from "./sparkle-effect"
import { ArrowRight, ChevronLeft, ChevronRight } from "lucide-react"
import Image from "next/image"

interface MemoryGalleryProps {
  onContinue: () => void
}

interface Memory {
  id: number
  src: string
  caption: string
  subtitle?: string
}

const memories: Memory[] = [
  { id: 1, src: "/fromwe.jpg", caption: "The Beginning", subtitle: "Where our story started..." },
  { id: 2, src: "/bubu smile.jpg", caption: "Her smile? Dangerous weapon.", subtitle: "Still gets me every time" },
  { id: 3, src: "/First meet.jpg", caption: "The day my heart did a backflip.", subtitle: "I knew right then..." },
  { id: 4, src: "/Our smile.jpg", caption: "The day we laughed like idiots", subtitle: "For 30 minutes straight" },
  { id: 5, src: "/4.jpg", caption: "My safe place", subtitle: "Home is wherever you are" },
  { id: 6, src: "/5.jpg", caption: "Counting stars with you", subtitle: "Best nights of my life" },
  { id: 7, src: "/3.jpg", caption: "Every dance with you", subtitle: "Feels like floating" },
  { id: 8, src: "/6.jpg", caption: "Forever My Favorite Person", subtitle: "Yesterday, today, always" },
  { id: 9, src: "/1.jpg", caption: "The Life I cant Imagine", subtitle: "Yesterday, today, always" },

]

export function MemoryGallery({ onContinue }: MemoryGalleryProps) {
  const [isBookOpen, setIsBookOpen] = useState(false)
  const [currentPage, setCurrentPage] = useState(0)
  const [isFlipping, setIsFlipping] = useState(false)
  const [flipDirection, setFlipDirection] = useState<"next" | "prev" | null>(null)
  const [isExiting, setIsExiting] = useState(false)
  const [showHearts, setShowHearts] = useState(false)
  const [captionVisible, setCaptionVisible] = useState(false)
  const [isLastPage, setIsLastPage] = useState(false)

  const totalPages = memories.length

  // Show caption animation when page changes
  useEffect(() => {
    if (isBookOpen && !isFlipping) {
      setCaptionVisible(false)
      const timer = setTimeout(() => setCaptionVisible(true), 300)
      return () => clearTimeout(timer)
    }
  }, [currentPage, isBookOpen, isFlipping])

  // Check if last page
  useEffect(() => {
    setIsLastPage(currentPage === totalPages - 1)
  }, [currentPage, totalPages])

  const openBook = () => {
    setIsBookOpen(true)
    setTimeout(() => setShowHearts(true), 700)
  }

  const flipPage = useCallback(
    (direction: "next" | "prev") => {
      if (isFlipping) return
      if (direction === "next" && currentPage >= totalPages - 1) return
      if (direction === "prev" && currentPage <= 0) return

      setIsFlipping(true)
      setFlipDirection(direction)
      setShowHearts(true)

      // flip timing matches CSS animation (600ms)
      setTimeout(() => {
        setCurrentPage((prev) => (direction === "next" ? prev + 1 : prev - 1))
        setIsFlipping(false)
        setFlipDirection(null)
        setTimeout(() => setShowHearts(false), 900)
      }, 620)
    },
    [isFlipping, currentPage, totalPages],
  )

  const handleContinue = () => {
    setIsExiting(true)
    setTimeout(onContinue, 700)
  }

  // Touch/Swipe handling
  const [touchStart, setTouchStart] = useState<number | null>(null)
  const handleTouchStart = (e: React.TouchEvent) => setTouchStart(e.touches[0].clientX)
  const handleTouchEnd = (e: React.TouchEvent) => {
    if (touchStart === null) return
    const touchEnd = e.changedTouches[0].clientX
    const diff = touchStart - touchEnd

    if (Math.abs(diff) > 50) {
      if (diff > 0) flipPage("next")
      else flipPage("prev")
    }
    setTouchStart(null)
  }

  const currentMemory = memories[currentPage]

  return (
    <div
      className={`min-h-screen relative flex flex-col items-center justify-center overflow-hidden transition-opacity duration-700 ${isExiting ? "opacity-0" : "opacity-100"}`}
      style={{
        background: "linear-gradient(180deg, #fdf2f8 0%, #fce7f3 50%, #fbcfe8 100%)",
      }}
    >
      <SparkleEffect />

      {/* Floating heart particles on page flip */}
      {showHearts && isBookOpen && (
        <div className="fixed inset-0 pointer-events-none z-30">
          {[...Array(12)].map((_, i) => (
            <div
              key={i}
              className="absolute text-pink-400 animate-float-heart"
              style={{
                left: `${20 + Math.random() * 60}%`,
                bottom: "30%",
                animationDelay: `${i * 0.1}s`,
                fontSize: `${12 + Math.random() * 16}px`,
              }}
            >
              ❤
            </div>
          ))}
        </div>
      )}

      <div className="relative z-20 w-full max-w-4xl mx-auto px-4 py-8">
        {/* CLOSED BOOK VIEW */}
        {!isBookOpen && (
          <div className="flex flex-col items-center justify-center animate-fade-in">
            {/* 3D Book Cover */}
            <div
              onClick={openBook}
              className="relative cursor-pointer transform hover:scale-105 transition-all duration-500 group"
              style={{ perspective: "1000px" }}
            >
              {/* Book shadow */}
              <div className="absolute -bottom-4 left-1/2 -translate-x-1/2 w-[90%] h-8 bg-black/20 rounded-full blur-xl" />

              {/* Book body */}
              <div
                className="relative w-72 h-96 md:w-80 md:h-[420px] rounded-r-lg rounded-l-sm overflow-hidden group-hover:rotate-y-[-5deg] transition-transform duration-500"
                style={{
                  background: "linear-gradient(135deg, #fecdd3 0%, #fda4af 50%, #fb7185 100%)",
                  boxShadow: "8px 8px 20px rgba(0,0,0,0.3), inset -3px 0 10px rgba(0,0,0,0.1)",
                  transformStyle: "preserve-3d",
                }}
              >
                {/* Book spine effect */}
                <div
                  className="absolute left-0 top-0 bottom-0 w-6"
                  style={{
                    background: "linear-gradient(90deg, #f43f5e 0%, #fb7185 100%)",
                    boxShadow: "inset -2px 0 5px rgba(0,0,0,0.2)",
                  }}
                />

                {/* Decorative border */}
                <div className="absolute inset-4 left-8 border-2 border-white/30 rounded-lg" />

                {/* Heart decoration */}
                <div className="absolute top-8 left-1/2 -translate-x-1/2 text-white/80 text-5xl animate-pulse-soft">
                  ❤
                </div>

                {/* Book title */}
                <div className="absolute inset-0 flex flex-col items-center justify-center px-8 text-center">
                  <h2
                    className="text-white text-2xl md:text-3xl mb-2 drop-shadow-lg"
                    style={{ fontFamily: "'Playfair Display', serif" }}
                  >
                    Our Memory Book
                  </h2>
                  <p className="text-white/80 text-sm">A collection of us</p>
                </div>

                {/* Corner ribbon */}
                <div className="absolute top-0 right-0 w-20 h-20 overflow-hidden">
                  <div
                    className="absolute top-4 -right-8 w-32 text-center text-xs text-white py-1 rotate-45"
                    style={{ background: "#be123c" }}
                  >
                    With Love
                  </div>
                </div>

                {/* Bottom ribbon bookmark */}
                <div
                  className="absolute bottom-0 right-12 w-4 h-16 rounded-b-full"
                  style={{ background: "linear-gradient(180deg, #be123c 0%, #9f1239 100%)" }}
                />
              </div>
            </div>

            {/* Tap hint */}
            <p className="mt-8 text-foreground/70 text-lg animate-pulse-soft">Tap to open your Memory Book</p>
          </div>
        )}

        {/* OPEN BOOK VIEW */}
        {isBookOpen && (
          <div className="animate-book-open" onTouchStart={handleTouchStart} onTouchEnd={handleTouchEnd}>
            {/* Book container */}
            <div
              className="relative mx-auto"
              style={{
                perspective: "2000px",
                maxWidth: "700px",
              }}
            >
              {/* Book shadow */}
              <div className="absolute -bottom-6 left-1/2 -translate-x-1/2 w-[85%] h-10 bg-black/25 rounded-full blur-2xl" />

              {/* Open book structure */}
              <div className="relative flex" style={{ transformStyle: "preserve-3d" }}>
                {/* Left page (previous content preview) */}
                <div
                  className="w-1/2 aspect-[3/4] rounded-l-lg overflow-hidden relative page left-page"
                  style={{
                    background: "linear-gradient(90deg, #fef3e2 0%, #fefce8 100%)",
                    boxShadow: "inset 5px 0 15px rgba(0,0,0,0.1)",
                  }}
                >
                  {/* Page texture */}
                  <div
                    className="absolute inset-0 opacity-30"
                    style={{
                      backgroundImage: "url('/paper-texture-subtle.jpg')",
                      backgroundSize: "100px",
                    }}
                  />

                  {/* Left page content */}
                  <div className="relative h-full p-4 md:p-6 flex flex-col">
                    {/* Decorative corner */}
                    <div className="absolute top-2 left-2 text-pink-300/50 text-2xl">✿</div>
                    <div className="absolute bottom-2 right-2 text-pink-300/50 text-2xl">✿</div>

                    {/* Page number */}
                    <div className="text-center text-pink-400/60 text-xs mb-2">Page {currentPage + 1}</div>

                    {/* Decorative border */}
                    <div className="flex-1 border border-pink-200/50 rounded-lg p-3 flex flex-col items-center justify-center">
                      {/* Polaroid-style photo */}
                      <div
                        className="bg-white p-2 pb-8 shadow-lg transform -rotate-2 max-w-[85%] w-full aspect-square"
                        style={{ boxShadow: "3px 3px 10px rgba(0,0,0,0.15)" }}
                      >
                        {/* ---------- FIXED IMAGE WRAPPER (important) ---------- */}
                        {/* Next/Image with fill requires parent position:relative and fixed height */}
                        <div className="relative w-full h-full overflow-hidden rounded">
                          <Image
                            src={currentMemory.src || "/placeholder.svg"}
                            alt={currentMemory.caption}
                            fill
                            sizes="(max-width: 640px) 90vw, (max-width: 1024px) 45vw, 33vw"
                            className="object-contain"
                            priority
                          />
                        </div>
                        {/* ------------------------------------------------------ */}
                      </div>

                      {/* Tape decoration */}
                      <div
                        className="absolute top-16 left-1/2 -translate-x-1/2 w-12 h-4 bg-yellow-200/60 rotate-[-5deg]"
                        style={{ boxShadow: "0 1px 2px rgba(0,0,0,0.1)" }}
                      />
                    </div>
                  </div>
                </div>

                {/* Center spine */}
                <div
                  className="w-4 relative z-10"
                  style={{
                    background: "linear-gradient(90deg, #d4a574 0%, #c9956c 50%, #d4a574 100%)",
                    boxShadow: "0 0 10px rgba(0,0,0,0.3)",
                  }}
                />

                {/* Right page (main content) */}
                <div
                  className={`w-1/2 aspect-[3/4] rounded-r-lg overflow-hidden relative page right-page ${isFlipping && flipDirection === "next" ? "animate-page-flip-next" : ""} ${isFlipping && flipDirection === "prev" ? "animate-page-flip-prev" : ""}`}
                  style={{
                    background: "linear-gradient(270deg, #fef3e2 0%, #fefce8 100%)",
                    boxShadow: "inset -5px 0 15px rgba(0,0,0,0.1)",
                    transformOrigin: "left center",
                  }}
                >
                  {/* Page texture */}
                  <div
                    className="absolute inset-0 opacity-30"
                    style={{
                      backgroundImage: "url('/paper-texture-subtle.jpg')",
                      backgroundSize: "100px",
                    }}
                  />

                  {/* Right page content */}
                  <div className="relative h-full p-4 md:p-6 flex flex-col">
                    {/* Decorative hearts */}
                    <div className="absolute top-2 right-2 text-pink-300/50 text-xl">♥</div>

                    {/* Content */}
                    <div className="flex-1 flex flex-col items-center justify-center text-center px-2">
                      {/* Handwritten caption */}
                      <div
                        className={`transition-all duration-700 ${captionVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-4"}`}
                      >
                        <h3
                          className="text-xl md:text-2xl text-pink-700 mb-3 leading-relaxed"
                          style={{
                            fontFamily: "'Caveat', cursive",
                            textShadow: "1px 1px 2px rgba(0,0,0,0.1)",
                          }}
                        >
                          "{currentMemory.caption}"
                        </h3>

                        {currentMemory.subtitle && (
                          <p
                            className="text-pink-500/80 text-base md:text-lg italic"
                            style={{ fontFamily: "'Caveat', cursive" }}
                          >
                            {currentMemory.subtitle}
                          </p>
                        )}
                      </div>

                      {/* Small decorative doodle */}
                      <div className="mt-6 text-pink-300 text-3xl">∿∿∿</div>

                      {/* Sticky note decoration */}
                      <div
                        className="absolute bottom-8 right-4 w-16 h-16 flex items-center justify-center transform rotate-6"
                        style={{
                          background: "linear-gradient(135deg, #fef08a 0%, #fde047 100%)",
                          boxShadow: "2px 2px 5px rgba(0,0,0,0.15)",
                        }}
                      >
                        <span className="text-2xl">💕</span>
                      </div>
                    </div>

                    {/* Page number */}
                    <div className="text-center text-pink-400/60 text-xs">
                      {currentPage + 1} of {totalPages}
                    </div>
                  </div>

                  {/* page thickness edge */}
                  <div className="page-edge-right" />
                </div>

                {/* Page flip effect overlay */}
                {isFlipping && (
                  <div
                    className="absolute right-0 top-0 w-1/2 aspect-[3/4] pointer-events-none"
                    style={{
                      background: "linear-gradient(90deg, transparent 0%, rgba(0,0,0,0.1) 100%)",
                      transformOrigin: "left center",
                    }}
                  />
                )}
              </div>

              {/* Navigation arrows */}
              <button
                onClick={() => flipPage("prev")}
                disabled={currentPage === 0 || isFlipping}
                className="absolute left-0 top-1/2 -translate-y-1/2 -translate-x-12 md:-translate-x-16 w-10 h-10 md:w-12 md:h-12 bg-white/80 backdrop-blur rounded-full flex items-center justify-center shadow-lg hover:bg-white transition-all disabled:opacity-30 disabled:cursor-not-allowed hover:scale-110"
              >
                <ChevronLeft className="w-5 h-5 md:w-6 md:h-6 text-pink-600" />
              </button>

              <button
                onClick={() => flipPage("next")}
                disabled={currentPage === totalPages - 1 || isFlipping}
                className="absolute right-0 top-1/2 -translate-y-1/2 translate-x-12 md:translate-x-16 w-10 h-10 md:w-12 md:h-12 bg-white/80 backdrop-blur rounded-full flex items-center justify-center shadow-lg hover:bg-white transition-all disabled:opacity-30 disabled:cursor-not-allowed hover:scale-110"
              >
                <ChevronRight className="w-5 h-5 md:w-6 md:h-6 text-pink-600" />
              </button>

              {/* Tap zones for page flip */}
              <div
                className="absolute left-0 top-0 w-1/3 h-full cursor-pointer z-10 opacity-0"
                onClick={() => flipPage("prev")}
              />
              <div
                className="absolute right-0 top-0 w-1/3 h-full cursor-pointer z-10 opacity-0"
                onClick={() => flipPage("next")}
              />
            </div>

            {/* Page indicator dots */}
            <div className="flex justify-center gap-2 mt-6">
              {memories.map((_, index) => (
                <div
                  key={index}
                  className={`w-2 h-2 rounded-full transition-all duration-300 ${
                    index === currentPage ? "bg-pink-500 w-4" : index < currentPage ? "bg-pink-300" : "bg-pink-200"
                  }`}
                />
              ))}
            </div>

            {/* Final page message & continue button */}
            {isLastPage && (
              <div className="mt-8 text-center animate-fade-in">
                <p
                  className="text-pink-700 text-lg md:text-xl mb-6 px-4 leading-relaxed"
                  style={{ fontFamily: "'Caveat', cursive" }}
                >
                  "Thank you for creating these beautiful memories with me.
                  <br />
                  More pages will be added… with every moment we share."
                </p>

                <button
                  onClick={handleContinue}
                  className="group px-8 py-4 bg-gradient-to-r from-pink-500 to-rose-500 text-white rounded-full font-semibold text-lg shadow-lg hover:shadow-xl transform hover:scale-105 transition-all duration-300 flex items-center gap-3 mx-auto"
                >
                  Let's Play Some Games
                  <ArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform" />
                </button>
              </div>
            )}

            {/* Swipe hint */}
            {!isLastPage && currentPage === 0 && (
              <p className="text-center text-pink-400/70 text-sm mt-4 animate-pulse-soft">
                Swipe or tap arrows to flip pages
              </p>
            )}
          </div>
        )}
      </div>

      {/* Custom styles for animations */}
      <style jsx global>{`
        @import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=Playfair+Display:wght@400;500;600;700&display=swap');
        
        @keyframes float-heart {
          0% {
            transform: translateY(0) scale(1);
            opacity: 1;
          }
          100% {
            transform: translateY(-150px) scale(0.5);
            opacity: 0;
          }
        }
        
        @keyframes book-open {
          0% {
            transform: scale(0.8) rotateY(-30deg);
            opacity: 0;
          }
          100% {
            transform: scale(1) rotateY(0deg);
            opacity: 1;
          }
        }
        
        @keyframes page-flip-next {
          0% { transform: rotateY(0deg) translateZ(0px); box-shadow: 0 20px 40px rgba(0,0,0,0.12) }
          20% { transform: rotateY(-25deg) translateZ(8px); box-shadow: 0 30px 60px rgba(0,0,0,0.18) }
          50% { transform: rotateY(-92deg) translateZ(18px); box-shadow: 0 40px 80px rgba(0,0,0,0.22); opacity: 0.95 }
          80% { transform: rotateY(-70deg) translateZ(6px); box-shadow: 0 18px 40px rgba(0,0,0,0.12) }
          100% { transform: rotateY(0deg) translateZ(0px); box-shadow: 0 12px 30px rgba(0,0,0,0.08) }
        }
        
        @keyframes page-flip-prev {
          0% { transform: rotateY(0deg) translateZ(0px); box-shadow: 0 20px 40px rgba(0,0,0,0.12) }
          20% { transform: rotateY(25deg) translateZ(8px); box-shadow: 0 30px 60px rgba(0,0,0,0.18) }
          50% { transform: rotateY(92deg) translateZ(18px); box-shadow: 0 40px 80px rgba(0,0,0,0.22); opacity: 0.95 }
          80% { transform: rotateY(70deg) translateZ(6px); box-shadow: 0 18px 40px rgba(0,0,0,0.12) }
          100% { transform: rotateY(0deg) translateZ(0px); box-shadow: 0 12px 30px rgba(0,0,0,0.08) }
        }
        
        .animate-float-heart { animation: float-heart 2s ease-out forwards; }
        .animate-book-open { animation: book-open 0.8s ease-out forwards; }
        .animate-page-flip-next { animation: page-flip-next 0.62s cubic-bezier(.2,.9,.2,1) forwards; transform-origin: left center; }
        .animate-page-flip-prev { animation: page-flip-prev 0.62s cubic-bezier(.2,.9,.2,1) forwards; transform-origin: left center; }
        
        .page { transform-style: preserve-3d; backface-visibility: hidden; position: relative; overflow: visible; }
        .page .page-texture { position: absolute; inset: 0; opacity: 0.28; background-image: url('/paper-texture-subtle.jpg'); background-size: 100px; pointer-events: none; }
        .page .page-curl { position: absolute; right: 0; top: 0; bottom: 0; width: 22%; pointer-events: none; background: linear-gradient(90deg, rgba(255,255,255,0.0) 0%, rgba(255,255,255,0.06) 30%, rgba(0,0,0,0.06) 75%); transform-origin: left center; mix-blend-mode: multiply; opacity: 0.9; }
        
        .page-edge-right, .page-edge-left {
          position: absolute; top: 8px; bottom: 8px; width: 8px; z-index: 40; pointer-events: none; border-radius: 2px; box-shadow: inset 0 1px 0 rgba(255,255,255,0.35);
        }
        .page-edge-right { right: -6px; background: linear-gradient(180deg, #f5e8d8, #e9d6c3); }
        .page-edge-left { left: -6px; background: linear-gradient(180deg, #e9d6c3, #f5e8d8); }
        
        .flip-shadow { position: absolute; right: 0; top: 0; width: 50%; height: 100%; pointer-events: none; z-index: 45; opacity: 0.9; }
        .flip-shadow.shadow-next { background: linear-gradient(90deg, rgba(0,0,0,0.0) 0%, rgba(0,0,0,0.12) 30%, rgba(0,0,0,0.22) 60%); animation: shadow-sweep-next 0.62s ease-in-out forwards; }
        .flip-shadow.shadow-prev { left: 0; right: auto; background: linear-gradient(270deg, rgba(0,0,0,0.0) 0%, rgba(0,0,0,0.12) 30%, rgba(0,0,0,0.22) 60%); animation: shadow-sweep-prev 0.62s ease-in-out forwards; }
        @keyframes shadow-sweep-next { 0% { transform: translateX(100%); opacity: 0 } 30% { opacity: 0.65 } 100% { transform: translateX(0%); opacity: 1 } }
        @keyframes shadow-sweep-prev { 0% { transform: translateX(-100%); opacity: 0 } 30% { opacity: 0.65 } 100% { transform: translateX(0%); opacity: 1 } }
        
        .animate-fade-in { animation: fadeIn 0.6s ease-out both; }
        @keyframes fadeIn { from { opacity: 0; transform: translateY(8px) } to { opacity: 1; transform: translateY(0) } }
        
        @keyframes float-heart { 0% { transform: translateY(0) scale(1); opacity: 1 } 100% { transform: translateY(-150px) scale(0.6); opacity: 0 } }
        .animate-pulse-soft { animation: pulseSoft 2s ease-in-out infinite; }
        @keyframes pulseSoft { 0% { opacity: 0.85 } 50% { opacity: 1 } 100% { opacity: 0.85 } }
        
        @media (max-width: 640px) {
          .page-curl { width: 28% }
          .page-edge-right, .page-edge-left { width: 6px; }
        }
      `}</style>
    </div>
  )
}
